.description {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  justify-content: space-between;
  padding-top: 57px;
  padding-left: 9.9vw;
  padding-right: 9.9vw;
  padding-bottom: 73px;

  outline: 2px solid green;

  @media (max-width: 1600px) {
    padding-top: 50px;
    padding-left: 10vw;
    padding-right: 10vw;
  }

  @media (max-width: 1280px) {
    padding-top: 45px;
    padding-bottom: 60px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.description > section {

}

.description h1 {
  font-family: inherit;
  font-size: 100px;
  line-height: calc(86 / 100 * 100%);
  font-weight: 700;
  color: var(--basic-main);

  @media (max-width: 1600px) {
    font-size: 83px;
  }

  @media (max-width: 1280px) {
    font-size: 67px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.description p {
  font-family: inherit;
  font-size: 30px;
  line-height: calc(32 / 30 * 100%);
  font-weight: 300;
  color: var(--basic-main);
  letter-spacing: -1.5px;

  @media (max-width: 1600px) {
    font-size: 27px;
  }

  @media (max-width: 1280px) {
    font-size: 21px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.description p:not(:last-child) {
  margin-bottom: 25px;

  @media (max-width: 1600px) {
    margin-bottom: 20px;
  }

  @media (max-width: 1280px) {
    margin-bottom: 18px;
  }

  @media (max-width: 992px) {

