.nav-footer {
  display: grid;
  grid-template-columns: 1fr 3fr;
  column-gap: 12.24vw;
  padding-top: 53px;
  padding-left: 95px;
  padding-right: 95px;
  padding-bottom: 57px;
  letter-spacing: -0.5px;

  @media (max-width: 1600px) {
    padding-top: 45px;
    padding-left: 78px;
    padding-right: 78px;
    padding-bottom: 50px;
  }

  @media (max-width: 1280px) {
    padding-top: 35px;
    padding-left: 63px;
    padding-right: 63px;
    padding-bottom: 40px;
  }

