.main-nav {
  display: grid;
  grid-template-columns: repeat(3, max-content);
  column-gap: 4.17vw;
  align-items: center;
  padding-left: 6.25vw;
  padding-right: 6.25vw;

  @media (max-width: 1600px) {
    column-gap: 4.38vw;
    padding-left: 5.63vw;
    padding-right: 5.63vw;
  }

  @media (max-width: 1280px) {
    column-gap: 4.69vw;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.main-nav__basket {
  position: relative;
  display: flex;
  width: 37px;
  height: 30px;
  text-decoration: none;
  justify-self: end;

  @media (max-width: 1600px) {
    width: 31px;
    height: 25px;
  }

  @media (max-width: 1280px) {
    width: 25px;
    height: 20px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.main-nav__basket span {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 36px;
  height: 36px;
  top: -0.63vw;
  left: 1.56vw;
  font-size: 17px;
  font-variation-settings: 'wght' 300, 'wdth' 75;
  color: var(--basic-white);
  background-color: var(--basic-main);
  border-radius: 50%;

  @media (max-width: 1600px) {
    width: 30px;
    height: 30px;
    font-size: 14px;
  }

  @media (max-width: 1280px) {
    width: 24px;
    height: 24px;
    font-size: 12px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.main-nav__toggle {
  display: none;
  position: absolute;
}
