.card-goods {
  display: grid;
  grid-template-columns: repeat(6, minmax(120px, 1fr));
  column-gap: 3.28vw;
  row-gap: 40px;

  @media (max-width: 1600px) {
    column-gap: 2.81vw;
  }

  @media (max-width: 1280px) {
    column-gap: 2.34vw;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.card-goods li {
  display: flex;
  flex-direction: column;
  height: 100%;
  text-align: center;
}

.card-goods a {
  cursor: pointer;
}

.card-goods__img {
  position: relative;
  display: flex;
  flex-grow: 1;
  align-items: flex-end;
  justify-content: center;
  cursor: pointer;
}

.card-goods__img--hit::before {
  top: 1.41vw;
  right: 1.67vw;
}

.card-goods__img--new::before {
  top: 3.39vw;
  right: 1.3vw;
}

.card-goods__img img {
  position: relative;
}

.card-goods__link {
  min-height: 80px;
  font-family: inherit;
  font-size: 23px;
  line-height: calc(28 / 23 * 100%);
  font-variation-settings: 'wght' 300, 'wdth' 70;
  letter-spacing: -0.8px;
  color: var(--basic-brand);
  text-decoration: none;
  margin-top: 18px;

  @media (max-width: 1600px) {
    font-size: 21px;
    margin-top: 15px;
  }

  @media (max-width: 1280px) {
    min-height: 60px;
    font-size: 17px;
    margin-top: 12px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.card-goods__price {
  margin-top: 30px;
}
