.label-goods {
  position: relative;
}

.label-goods::before {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: inherit;
  font-size: 23px;
  line-height: calc(18 / 23 * 100%);
  letter-spacing: -1px;
  color: var(--basic-white);
  text-align: center;
  background-repeat: no-repeat;
  background-size: 100% auto;
  z-index: 10;

  @media (max-width: 1600px) {
    font-size: 20px;
  }

  @media (max-width: 1280px) {
    font-size: 16px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.label-goods--hit::before {
  content: "хит продаж";
  width: 118px;
  height: 99px;
  background-image: url("../img/hit.svg");
  padding-top: 1.4vw;
  padding-left: 1.04vw;

  @media (max-width: 1600px) {
    width: 98px;
    height: 90px;
  }

  @media (max-width: 1280px) {
    width: 77px;
    height: 72px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.label-goods--new::before {
  content: "новинка";
  width: 98px;
  height: 63px;
  background-image: url("../img/new-shop.svg");
  padding-top: 0;
  padding-left: 0;

  @media (max-width: 1600px) {
    width: 98px;
    height: 63px;
  }

  @media (max-width: 1280px) {
    width: 70px;
    height: 42px;
  }

