.catalog-mashines {
  display: grid;
  grid-template-columns: repeat(5, minmax(180px, 384px));
  column-gap: 2.08vw;
  row-gap: 40px;
  justify-content: center;

  @media (max-width: 1600px) {
    column-gap: 1.88vw;
  }

  @media (max-width: 1280px) {
    column-gap: 1.8vw;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.catalog-mashines li {


}

.catalog-mashines__link {
  display: flex;
  flex-direction: column;
  height: 100%;
  text-decoration: none;
}

.catalog-mashines__img {
  flex-grow: 1;
  display: flex;
  align-items: flex-end;
  justify-content: center;
}

.catalog-mashines__text {
  min-height: 140px;
  text-align: center;
  margin-top: 40px;

  @media (max-width: 1600px) {
    min-height: 110px;
    margin-top: 35px;
  }

  @media (max-width: 1280px) {
    margin-top: 30px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.catalog-mashines__text span {
  display: flex;
  flex-direction: column;
}

.catalog-mashines__type {
  font-size: 28px;
  line-height: calc(26 / 28 * 100%);
  font-variation-settings: 'wght' 700, 'wdth' 75;
  letter-spacing: -0.5px;
  font-variant: small-caps;
  margin-bottom: 12px;

  @media (max-width: 1600px) {
    font-size: 25px;
  }

  @media (max-width: 1280px) {
    font-size: 21px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.catalog-mashines__name {
  font-size: 34px;
  line-height: calc(26 / 34 * 100%);
  font-variation-settings: 'wght' 700, 'wdth' 75;
  font-variant: small-caps;
  margin-bottom: 10px;

  @media (max-width: 1600px) {
    font-size: 28px;
  }

  @media (max-width: 1280px) {
    font-size: 23px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.catalog-mashines__brand {
  font-size: 25px;
  color: var(--basic-brand);
  font-weight: 300;

  @media (max-width: 1600px) {
    font-size: 21px;
  }

  @media (max-width: 1280px) {
    font-size: 17px;
