.advantages {
  max-width: 100%;
  background-image: url("../img/advantage-back.jpg");
  background-size: cover;
  color: var(--basic-white);
}

.advantages__wrap  {
  display: grid;
  grid-template-columns: max-content 1fr;
  align-items: center;
}

.advantages h2 {
  display: grid;
  color: var(--basic-white);
  padding-left: 150px;
}

.advantages__list {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, minmax(200px, 1fr));
  column-gap: 7vw;
  row-gap: 240px;
  align-items: center;
  justify-content: space-between;
  padding-left: 85px;
  padding-right: 85px;
  margin-top: 217px;

  @media (max-width: 1600px) {
    row-gap: 200px;
    padding-left: 64px;
    padding-right: 64px;
    margin-top: 180px;
  }

  @media (max-width: 1280px) {
    row-gap: 160px;
    margin-top: 145px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.advantages__item {
  position: relative;
}

.advantages__item::before {
  position: absolute;
  content: "";
  background-repeat: no-repeat;
  background-size: 100% auto;
}

