.price-card {
  position: relative;
  font-family: inherit;
  font-size: 25px;
  color: var(--basic-white);
  background: transparent;
  cursor: pointer;

  @media (max-width: 1600px) {
    font-size: 21px;
  }

  @media (max-width: 1280px) {
    font-size: 17px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.price-card::after {
  position: absolute;
  content: "";
  width: 143px;
  height: 50px;
  top: -0.68vw;
  left: 50%;
  transform: translateX(-50%);
  background-image: url("../img/price-shop.svg");
  background-repeat: no-repeat;
  background-size: 100% auto;
  z-index: -1;

  @media (max-width: 1600px) {
    width: 119px;
    height: 43px;
  }

  @media (max-width: 1280px) {
    width: 95px;
    height: 34px;
  }

